@import url(https://fonts.googleapis.com/css?family=Lato|Poppins&amp;display=swap);
@import url(https://fonts.googleapis.com/css?family=Poppins:700&amp;display=swap);
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}
.model:target{
  position: fixed;
  top: 0;
  left: 0;
  background-color: rgba(0, 0, 0, 0.3);
  width: 100%;
  height: 100vh;
  display: block !important;
  z-index: 100;
}
.model:target .modal-dialog{
  position: absolute;
  top: 50%;
  left: 50%;
  -webkit-transform: translate(-50%,-50%);
          transform: translate(-50%,-50%);
}
.feature-item{
  height: 360px !important;
}
.model{
  display: none;
}
.details .slick-dots > li{
  width: calc(100%/9) !important;
  bottom: 27.5px !important;
  -webkit-transition: all 0.3s ease-in-out;
  transition: all 0.3s ease-in-out;
}
.details .slick-dots > .slick-active{
  -webkit-transform: scale(1.2) translateY(-10px);
          transform: scale(1.2) translateY(-10px);
  z-index: 100;
}
.details .slick-arrow{
  display: block !important;
}
.details .slick-prev::before{
  content: "" !important;
}
.details .slick-next::before{
  content: "" !important;
}

.details .slick-prev::before , .details .slick-next::before{
  line-height: 2 !important;
}
.Cal__Day__end .Cal__Day__selection{
  color: #333 !important;
}
.Cal__Header__wrapper{
  display: block;
  width: 100%;
  margin-top: 50px !important;
}
.Cal__Header__wrapper:last-child{
  margin-left: 0 !important;
  margin-top: 100px !important;
}
.Cal__Header__range{
  display: block !important;
}

.Cal__Header__wrapper:first-child::before,
.Cal__Header__wrapper:first-child::after{
  top: 20% !important;
  margin-top: 0 !important;
}

.Cal__Header__root.Cal__Header__landscape{
  min-width: 270px !important;
}

.Cal__Header__wrapper span{
  color: #fff !important;
}

.Cal__Day__selection{
  background-color: rgb(254, 78, 55) !important;
  color: #fff !important;
  border-color: #fff !important;
}
.Cal__Day__selection span{
  color: #fff !important;
}
.Cal__Day__root .Cal__Day__month{
  top: -15px;
}

.Cal__Day__start,.Cal__Day__betweenRange,.Cal__Day__end{
  color: #facec8 !important;
}
.Cal__Day__betweenRange .Cal__Day__selection{
  background-color: #facec8 !important;
}
.Cal__Day__range.Cal__Day__selected.Cal__Day__end .Cal__Day__selection{
  border: none !important;
}

.filter-hide{
  -webkit-transition: all 0.5s ease-in;
  transition: all 0.5s ease-in;
  -webkit-transform: scale(0);
          transform: scale(0);
}
.filter-shown{
  -webkit-transform: scale(1);
          transform: scale(1);
}

.displayNone{
  display: none;
}
.displayBlock{
  display: block;
}
.phoneNo{
  position: absolute;
    top: 30%;
    left: 10%;
    -webkit-transform: translate(-10%, -30%);
            transform: translate(-10%, -30%);
}

input[type=text],
input[type=email],
input[type=number],
input[type=search],
input[type=password],
input[type=tel],
input[type=date],
textarea,
select,
.form-control {
  font-size: 14px;
  font-family: 'Roboto', sans-serif;
  font-weight: 300;
  background-color: #fff;
  border: 1px solid #eceaea;
  border-radius:0px;
  padding: 10px 25px;
  width: 100%;
  color: #444444;
  height: auto;
  margin-bottom: 0px; 
  box-shadow: none;
}
select{
  padding: 10px 20px;
}
input[type=text]:focus,
input[type=email]:focus,
input[type=number]:focus,
input[type=search]:focus,
input[type=password]:focus,
input[type=tel]:focus,
input[type=date]:focus,
textarea:focus,
select:focus {
  border-color: #66afe9;
  -webkit-transition: all 0.5s ease;
  transition: all 0.5s ease; 
}
textarea {
  resize: vertical;
  height: 200px; 
}
select {
	padding: 14px 18px;
	cursor: pointer;
}

input {
	-webkit-transition: all 0.1s ease-in-out;
	transition: all 0.1s ease-in-out;
}

input:focus,
input[type="text"]:focus,
input[type="password"]:focus,
input[type="email"]:focus,
input[type="number"]:focus,
textarea:focus {
	color: #808080;
	-webkit-transition: box-shadow 0.2s !important;
	transition: box-shadow 0.2s !important;
	box-shadow: 0px 0px 10px 0px rgba(0,0,0,0.07);
	border: 1px solid #d8d8d8;
	opacity: 1;
}

input[type="submit"] {
  border: none;
  padding: 11px 18px;
  width: auto;
}

input[type="checkbox"] { display: inline; }

.checkbox-block input[type="checkbox"] {
	display: none;
}

input[type="radio"] {
	width: 15px;
	height: 15px;
	cursor: pointer;
	box-shadow: none;
}

/* Input Placeholder Color */
::-webkit-input-placeholder { /* WebKit browsers */
	color: #888;
	opacity: 1;
}

:-moz-placeholder { /* Mozilla Firefox 4 to 18 */
	color: #888;
	opacity: 1;
}

::-moz-placeholder { /* Mozilla Firefox 19+ */
	color: #888;
	opacity: 1;
}

:-ms-input-placeholder { /* Internet Explorer 10+ */
	color: #888;
	opacity: 1;
}

textarea {
	height: auto;
	line-height: 27px;
	padding: 20px;
	min-height: 130px;
	-webkit-transition: none !important;
	transition: none !important;
	min-width: 100%;
}

label,
legend {
	display: block;
	font-weight: bold;
	font-size: 15px; 
	font-weight: normal;
	margin-bottom: 8px;
	line-height: 1.5;
}
label {
	font-size: 15px;
	font-weight: 500;
	color: #333;
	margin: 10px 0;
}

label span,
legend span {
	font-weight: normal;
	font-size: 14px;
	color: #444;
}

fieldset {
    padding: 0;
    border: none;
}


a, button { outline: none !important; }
a:focus,
a:hover { text-decoration: none; color: #333;}
img { max-width: 100%; }


/* ---------------------------------- */
/* Header
------------------------------------- */


#header {
	position: relative;
	z-index: 999;
	box-shadow: 0 0 8px 0 rgba(0, 0, 0, 0.12);
	font-size: 16px;
	background: #333;
}

/* Header Widget */
.header-widget {
	position: relative;
	top: 4px;
	height: 54px;
}

.header-widget .button,
.header-widget .button.border {
	margin-right: 0;
	min-width: 130px;
	text-align: center;
	position: relative;
	line-height: 26px;
	font-weight: 500;
}

.header-widget .button.border i {
	font-size: 17px;
	padding: 0px 0 0 4px;
	top: 2px;
	display: inline-block;
	position: relative;
}

.header-widget .sign-in {
	position: relative;
	color: #333;
	margin-right: 25px;
	top: 10px;
	display: inline-block;
	vertical-align: top;
}

.header-widget .sign-in i { padding-right: 4px; font-size: 15px; }
.header-widget .sign-in:hover { color: #66676b; }







/*----------------------------------- */
/* Footer
------------------------------------- */

/* Footer Bottom */
.copyrights {
	color: #707070;
	border-top: 1px solid rgba(0,0,0,0.09);
	margin-top: 60px;
	text-align: center;
	padding: 40px 0;
	display: inline-block;
	width: 100%;
}

#footer.alt .copyrights { 
	margin-top:0;
	border: none;
	padding: 25px;
}

.copyrights a { color: #707070; }



/* ---------------------------------- */
/* Sort by custom select
------------------------------------- */
.sort-by {
	position: relative;
	top: 9px;

}
.sort-by label,
.sort-by .sort-by-select {
	display: block;
	float: right;
}


.sort-by-select select {
	border-radius: 5px;
	color: #777;
	border: none;
	background-color: #f0f0f0;
	height: auto;
	padding: 7px 7px 7px 15px;
	line-height: 22px;
	box-shadow: none;
}

.sort-by label {
	line-height: 40px;
	margin-top: 1px;
	padding: 0;
}

.sort-by .chosen-container-single .chosen-single {
    height: 36px;
    line-height: 34px;
    border: 1px solid #fff;
    background-color: #fff;
    width: 100%;
    display: inline-block;
    padding: 0 14px 0 16px;
    box-shadow: none;
    color: #666;
    font-weight: 600;
    font-size: 15px;
    border-radius: 50px;
    -webkit-transition: all 0.3s;
    transition: all 0.3s;
    display: inline-block;
}

.sort-by .chosen-container-active .chosen-choices,
.sort-by .chosen-container-active.chosen-with-drop .chosen-single,
.sort-by .chosen-single:hover {
    background: #f3f3f3;
}

.sort-by .chosen-container .chosen-drop {
	padding-top: 10px;
    border: 1px solid #e0e0e0;
    border-top: 1px solid #e0e0e0;
    border-radius: 3px;
    width: 200px;
    margin-left: 10px;
    background: #fff;
	box-shadow: 0px 0px 8px 0px rgba(0,0,0,0.08);
	z-index: 999;
}


.sort-by .chosen-container.chosen-with-drop .chosen-drop,
.sort-by .chosen-container .chosen-drop {left: auto; right: -1px; }

.sort-by .chosen-container-single .chosen-single div:after {
	color: #66676b;
	right: 0;
}

.sort-by .chosen-container-single .chosen-default { color: #66676b; }

.sort-by .chosen-container-single .chosen-single span {
	margin-right: 0px;
	color: #666;
	font-weight: 600;
	-webkit-transition: all 0.2s;
	transition: all 0.2s;
}

.sort-by .chosen-container-single .chosen-single div {
    position: relative;
    line-height: 33px;
    top: 0;
	margin-left: 0;
    display: inline-block;
}

.sort-by .chosen-container-single .chosen-single span {
    display: inline-block;
    margin-right: 0;
    vertical-align: top;
}




/* ---------------------------------- */
/* Input with dropdown
------------------------------------- */
.select-input {
	position: relative;
}

.select-input input { position: relative; z-index: 101; }

.select-input .select {
	position: absolute;
	top: -8px;
}

.select-hidden {
	display: none;
	visibility: hidden;
}

.select {
	cursor: pointer;
	display: inline-block;
	position: relative;
	font-size: 15px;
	color: #fff;
	width: 100%;
	height: 51px;
}

.select-styled { opacity: 0; }

.select-options {
	display: none;
	position: absolute;
	top: 100%;
	right: 0;
	left: 0;
	z-index: 990;
	margin: 0;
	padding: 0;
	list-style: none;
	border: 1px solid #e0e0e0;
	background-color: #fcfcfc;
	padding: 0 4px;
	padding-top: 8px;
	border-top: 0;
	border-radius: 0 0 4px 4px;
	box-sizing: border-box;
}

.select-list-container {
	padding: 0 4px;
	max-height: 300px;
	overflow: auto;
	display: inline-block;
	width: 100%
}

ul.select-options li {
	margin: 0;
	padding: 0;
	-webkit-transition: all 0.15s ease-in;
	transition: all 0.15s ease-in;
	color: #888;
	margin: 0;
	padding: 10px 10px;
	list-style: none;
	line-height: 15px;
	border-radius: 4px;
}

.select-options li:hover {
	color: #fff;
	background-color: #66676b;
}

.select-options li[rel="hide"] {
  display: none;
}

.select-options ::-webkit-scrollbar { width: 10px; }
 
.select-options ::-webkit-scrollbar-track {
    background-color: rgba(0,0,0,0.05);
    border-radius: 10px;
}
 
.select-options ::-webkit-scrollbar-thumb {
    border-radius: 10px;
    background-color: rgba(0,0,0,0.2);
}
 
 .select-options ::-webkit-scrollbar-thumb:hover {
    border-radius: 10px;
    background-color: rgba(0,0,0,0.3);
}







/* Comments
------------------------------------- */
.comments { margin: -10px 0 0 0; }

.comments h4 {
	margin-bottom: 25px;
	font-size: 20px;
	font-weight: 500;
	color: #333;
}

.comments h4 span { display: inline-block; font-size: inherit; }

.comment { font-size: 20px; }

.comments .button {
	margin: 0 0 10px  0;
	padding: 7px 15px;
}

.comments.reviews .button { margin: 0; }

.comments ul {padding-left: 0;}
.comments ul li { display: block; width: 100%; }

.comments ul li,
.comments ul li ul li,
.comments ul li ul li ul li,
.comments ul li ul li ul li {
	margin: 50px 0 0 0px;
}

.comments ul li ul { margin: 0 0 0 100px; }

.comment-content p {
	margin: 3px 0 0 0;
	line-height: 26px;
}

.comment-content {
	color: #666;
	padding: 0 0 0 100px;
}

.comments ul li ul {
	border-left: 1px solid #e0e0e0;
	padding-left: 35px;
}

.comments ul li ul li:before {
	content: "";
	width: 15px;
	height: 1px;
	background-color: #e0e0e0;
	display: inline-block;
	position: absolute;
	top: 30px;
	left: -35px;
}

.comments ul li { position: relative; }

.comment-content strong {
	padding-right: 5px;
	color: #666;
}

.comment-content span { color: #888; }
body .comment-content p { padding: 5px 0; font-size: 15px; }

.comments-amount { color: #888; font-weight: 500; }

.avatar {
	display: block;
	float: left;
	padding: 0 20px 0 0;
	position: absolute;
	left: 0px;
}

.avatar img {
	max-width: 80px;
	border-radius: 50%;
}

.comment-by {
	width: 100%;
	padding-bottom: 8px;
	padding-top: 5px;
	font-weight: 500;
	color: #333;
	display: inline-block;
	width: 100%;
}

.comment-by h5 { margin: 0 0 7px 0; }

.comment-by a.reply,
.comment-by a.reply i {
	-webkit-transition: all 0.2s ease-in-out;
	transition: all 0.2s ease-in-out;
}

.comment-by a.reply {
	display: inline;
	background-color: #f0f0f0;
	padding: 5px 16px;
	line-height: 22px;
	font-size: 13px;
	color: #333;
	font-weight: 500;
	position: absolute;
	right: 0;
	top: 15px;
	border-radius: 50px;
}

.comment-by a.reply i {
	font-size: 12px;
	margin-right: 1px;
}

.comment-by .numerical-rating {
	margin: 4px 0 0 0;
}

.comment-by .numerical-rating,
.comment-by .star-rating {
	position: inherit;
	right: 0;
	top: 0;
}

.comment-by a.reply:hover { background-color: #66676b; }
.comment-by a.reply:hover,
.comment-by a.reply:hover i {
	color: #fff;
}

.comment-by span.date {
	color: #888;
	font-weight: 300;
	margin: 0px 0 0 0;
	float: none;
	display: block;
	text-align: left;

}


/* Listing Reviews */
.listing-reviews ul li {
	padding-bottom: 45px;
	margin-top: 40px;
	border-bottom: 1px solid #e8e8e8;
}

/* Review Images */
.review-images {
	display: flex;
	margin-top: 20px;
	margin-bottom: 5px;
}

.review-images a {
	flex: 4 1;
	max-width: 25%;
	margin-right: 15px;
	border-radius: 3px;
}

.review-images a img {border-radius: 4px; }


/* Add Comment */
.add-review-box {
	border-radius: 4px;
	background-color: #f9f9f9;
	padding: 36px;
	padding-bottom: 40px;
	margin-bottom: 55px;
	margin-top: 70px;
	display: inline-block;
	width: 100%;
	position: relative;
}

.add-review-box h3 { margin-top: 5px }


.add-comment label span { color: #e24747; }

.add-comment input {
	margin: 0 0 25px 0;
	box-sizing: border-box;
}

.add-comment textarea,
.add-comment input {
	box-shadow: 0 1px 3px 0px rgba(0, 0, 0, 0.05)
}

.add-comment textarea {
	margin: 0 0 30px 0;
	width: 100%;
	min-width: 100%;
	min-height: 220px;
	resize: vertical;
	-webkit-transition: none;
	transition: none;
	width: 100%;
	box-sizing: border-box;
}

.add-comment .button { min-width: 160px; }


/* Leave Rating */
.leave-rating {
	height: 24px;
	float: left;
}

.leave-rating:hover input[type="radio"]:checked ~ label { color: #dadada; }

.leave-rating input[type="radio"] { display: none; }

.leave-rating input[type="radio"]:checked ~ label { color: #ffc600; }

.leave-rating label {
	font-size: 24px;
	float: right;
	letter-spacing: 4px;
	color: #dadada;
	cursor: pointer; 
	-webkit-transition: 0.3s; 
	transition: 0.3s;
}

.leave-rating label:hover,
.leave-rating label:hover ~ label {
	color: #ffc600 !important;
}

.leave-rating-title {
	display: block;
	margin: 35px 0 7px 0;
}


/* Upload Photos Button */
.add-review-photos {
	font-weight: 600;
	padding: 6px 7px;
	color: #666;
	background-color: #ededed;
	border-radius: 50px;
	display: block;
	position: relative;
	font-size: 14px;
	width: auto;
	-webkit-transition: 0.3s;
	transition: 0.3s;
	cursor: pointer;
	line-height: 20px;
	float: right;
	top: -25px;
}

.photoUpload {
    position: relative;
    overflow: hidden;
    padding: 3px 12px;
	cursor: pointer;
}

.photoUpload input.upload {
    position: absolute;
    top: 0;
    right: 0;
    margin: 0;
    padding: 0;
    cursor: pointer;
    opacity: 0;
    filter: alpha(opacity=0);
}

.add-review-photos:hover {
	color: #fff;
	background-color: #66676b; 
}

.add-review-photos i {
	color: #66676b;
	margin-right: 3px;
	-webkit-transition: 0.3s;
	transition: 0.3s;
	font-size: 16px;
	position: relative;
	top: 2px;
}

.add-review-photos:hover i { color: #fff; }

/* Helpfup Review */
a.rate-review {
	border: 1px solid #e0e0e0;
	border-radius: 50px;
	font-size: 13px;
	color: #666;
	font-weight: 500;
	padding: 5px 20px;
	margin-top: 20px;
	display: inline-block;
	-webkit-transition: 0.3s;
	transition: 0.3s;
}

a.rate-review span {
	border-left: 1px solid #e0e0e0;
	line-height: 20px;
	padding-left: 10px;
	margin-left: 5px;
	-webkit-transition: 0.3s;
	transition: 0.3s;
	color: #666;
}

a.rate-review i { padding-right: 3px; }

a.rate-review:hover span { border-color: #d0d0d0 }

a.rate-review:hover {
	border-color: #d0d0d0;
	background-color: #fafafa;
}


/* ---------------------------------- */
/* Pagination
------------------------------------- */
.pagination-container{margin-top: 15px; text-align: center;}

.dash-list .pagination-container, .dash-list .pagination{margin: 0; text-align: center;}

.pagination {
	margin: 9px 0 0 0;
	text-align: center;
	overflow: hidden;
}

.pagination-next-prev {
	position: relative;
	top: -66px;
}

.pagination ul{background: transparent!important;}

.pagination ul li {
	display: inline-block;
	margin: 0;
	padding: 0!important;
	border: none!important;
}

.pagination ul li:hover{background: transparent!important;}


.pagination ul li a,
.pagination-next-prev ul li a {
	padding: 12px 0;
	border-bottom: none;
	display: inline-block;
	color: #333;
	background-color: #fff;
	font-weight: 700;
	margin:0;
	line-height: 22px;
	-webkit-transition: all 200ms ease-in-out;
	transition: all 200ms ease-in-out;
	font-size: 14px;
}

.pagination ul li a {
	border-radius: 50%;
	width: 52px;
	height: 52px;
	padding: 0;
	line-height: 52px;
}

.pagination ul li a i {
	line-height: 52px;
}

.pagination ul li.blank {
	color: #a0a0a0;
	padding: 0 6px;
}

.pagination ul li a.current-page,
.pagination .current,
.pagination ul li a:hover,
.pagination-next-prev ul li a:hover {
	background-color: #f2f2f2;
	color: #333;
}

.pagination-next-prev a.next { float: right; }
.pagination-next-prev a.prev { float: left; }

.pagination-next-prev ul li a {
	color: #333;
	padding: 12px 16px;
	text-transform: uppercase;
}

.pagination ul,
.pagination-next-prev ul {	padding: 0; list-style-type: none;}


/* Listings Pagination */
.fs-listings .pagination ul li a,
.fs-listings .pagination-next-prev ul li a {
	background-color: transparent;
}

.fs-listings .pagination ul li a.current-page,
.fs-listings .pagination .current,
.fs-listings .pagination ul li a:hover,
.fs-listings .pagination-next-prev ul li a:hover {
	background-color: #e8e8e8;
	color: #333;
}





/* ------------------------------------------------------------------- */
/*  03. Shortcodes
---------------------------------------------------------------------- */

/* ---------------------------------- */
/* Buttons
------------------------------------- */

button.button,
input[type="button"],
input[type="submit"],
a.button.border,
a.button {
	background-color: #66676b;
	top: 0;
	padding: 9px 20px;
	color: #fff;
	position: relative;
	font-size: 15px;
	font-weight: 600;
	display: inline-block;
	-webkit-transition: all 0.3s ease-in-out;
	transition: all 0.3s ease-in-out;
	cursor: pointer;
	margin-right: 6px;
	overflow: hidden;
	border: none;
	border-radius: 5px;
}


input[type="button"],
input[type="submit"] {
	line-height: 32px;
}

button.button:before,
a.button:before {
	width: 100%;
	height: 100%;
	content: "";
	display: block;
	background-color: #fff;
position: absolute;
	left: 0;
	top: 0;
