	.listing-nav-container.cloned .listing-nav li:first-child a:hover,
	.listing-nav li:first-child a,
	.listing-nav li a.active,
	.listing-nav li a:hover {
	    border-color: #e8e8e8;
	}

	.dashboard-content .checkboxes label {
    	width: 100%;
	}

	.dashboard-wrapper.dash-add .edit-profile-photo {
    	margin:0 0 15px;
	}

	.trip-detail .col-xs-12.col-sm-6.col-md-6:first-child{margin-bottom: 15px;}

	.about-author img {
	    width: 70px;
	    height: 70px;
	}

	.about-description {
	    margin: 2px 0 0 100px;
	}
	/* Blog styling */
	#posts-nav li {
	    position: relative;
	    width: 100%;
	}

	#posts-nav li a { padding: 0 !important; }
	#posts-nav li a::before { display: none;}
	#posts-nav li.next-post {text-align: left; margin-bottom: 30px;}	.comments ul li ul { margin: 0;  }

	.avatar img {  max-width: 60px;  }
	.comment-content {   padding: 0 0 0 85px; }
	.comments ul li ul li::before { top: 25px; }
	.comment-content {
	    padding: 0;
	}

	.testimonial::after { display: none; }
	.testimonial-box { padding-left: 38px; }
	
	.content-left h2{
		width: 100%;
	}
	.dashboard-nav{
		top: 155px;
	}
	ul.dropdown-menu{
		left: 0;
		right: unset;
		min-width: 250px;
	}
	#dashboard .copyrights{
		padding-left: 0;
	}
}


@media (max-width: 480px) { 
	.tabs-nav.blog li,
	.tabs-nav.blog li a,
	.tabs-nav li,
	.tabs-nav li a {
		width: 100%;
		padding: 0;
		border-radius: 3px;
		text-align: center;
		border-bottom: none;
	}

	.about-author img {
	    display: none;
	}

	.about-description {
	    margin: 0;
	}
	
	.user-menu {  margin: 10px 0 20px 25px; top: 0; display: block; }

	.header-widget .sign-in {
		position: relative;
		display: block;
		top: 0px;
		margin-bottom: 10px;
		margin-right: 25px;
	}

	.dashboard-list-box .sort-by .sort-by-select {display: none;}
	.search-bar{
		width: 100%;
		border-bottom: 1px solid #eee;
	}
		a.dashboard-responsive-nav-trigger{
		top: 255px;
	}
	.dashboard-content{
		padding-top: 280px;
	}
	.dashboard-nav{
		top: 255px;
	}
	.col-xs-6{
		width: 100%;
	}
	.dropdown:last-child .dropdown-item{
		border: none;
	}
}

@media(max-width: 400px){
	.content-right{display: none;}

	a.dashboard-responsive-nav-trigger {top: 70px; position: relative;}

	.dashboard-content {padding-top: 92px;}

	.dashboard-nav {top: 70px;}

	.dashboard-list-box h4{font-size: 15px;}
}


/*Dashboard Booking*/
.booking-table td{
	font-weight: 500;
}
.booking-table span.t-box{
	color: #fff;
	background: #ccc;
	padding: 8px 15px;
	border: 1px solid #ccc;
	border-radius: 30px;
}
.booking-table span.paid,
.booking-table span.approved{
	background: #468c21;
	border-color: #468c21;
}
.booking-table span.unpaid,
.booking-table span.cancel{
	background: #D60D45;
	border-color: #D60D45;
}
.booking-table span.pending{
	background: #a95921;
	border-color: #a95921;
}
.booking-table a.button.gray{
    color: #fff;
    margin: 0 2px;
}
.booking-table .button i{
	padding: 0;
}
