/* ========================================= */
/*          Default CSS                      */
/* ========================================= */

* {
  margin: 0;
  padding: 0;
  border: 0;
  outline: 0;
  font-size: 100%;
  background: transparent;
  box-sizing: border-box; 
}

ol, ul {list-style: none; }

:focus {outline: 0; }

.clear {clear: both; line-height: 0; font-size: 0; }

.clearfix:after {
  clear: both;
  content: '.';
  display: block;
  visibility: hidden;
  height: 0; 
}
.clearfix:after .test {color: red; }

.clearfix {display: inline-block; }

* html .clearfix {height: 1%; }

.clearfix {display: block; }

header,
nav,
section,
article,
aside,
footer {
  display: block; }

* {
  margin: 0;
  padding: 0; }

body {
  margin: 0;
  padding: 0;
  font-size: 15px;
  font-family: 'Lato', sans-serif;
  overflow-x: hidden; 
}

/* Preloader */

.preloader {
  background-color: #F9B522;
  height: 100%;
  left: 0;
  position: fixed;
  top: 0;
  width: 100%;
  z-index: 9999999;
}
.preloader .spinner {
  width: 60px;
  height: 60px;
  position: absolute;
  top: 50%;
  left: 50%;
  margin-top: -30px;
  margin-left: -30px;
  background-color: #242C33;
  border-radius: 100%;
  -webkit-animation: sk-scaleout 1.0s infinite ease-in-out;
  animation: sk-scaleout 1.0s infinite ease-in-out;
}
@-webkit-keyframes sk-scaleout {
  0% {
    -webkit-transform: scale(0);
  }
  100% {
    -webkit-transform: scale(1);
    opacity: 0;
  }
}
@keyframes sk-scaleout {
  0% {
    -webkit-transform: scale(0);
    transform: scale(0);
  }
  100% {
    -webkit-transform: scale(1);
    transform: scale(1);
    opacity: 0;
  }
}

#preloader{
  position: fixed;
  top: 0;
  left: 0;
  right: 0;
  bottom: 0;
  background: #fff;
  z-index: 999999;
}
#status{
  position: fixed;
  content: '';
  display: block;
  top: 25%;
  left: 0;
  right: 0;
  width: 300px;
  height: 300px;
  margin: 0 auto;
  background: url(/adeebAhmad1/Hotux.git/static/media/loader.a74491cd.gif);
  background-size: cover;
  background-position: center;
  opacity: 1;
  visibility: visible;
  -webkit-transition: all 0.3s ease-in-out;
  transition: all 0.3s ease-in-out;
  z-index: 9999999999;
}
/* End Preloader */

h1,h2,h3,h4,h5,h6{
  font-weight: 700;
  color: #343a40;
  font-family: 'Poppins', sans-serif;
  margin:0 0 15px;
  line-height: 1.4;
  text-transform: uppercase;
}

h1 {
  font-size: 48px;
}

h2 {
  font-size: 36px; 
}

h3 {
  font-size: 26px;
}

h4 {
  font-size: 20px;
}

h5 {
  font-size: 15px;
}

h6 {
  font-size: 13px;
}

@media(max-width: 991px){
  h2{font-size: 28px;}
}

@media(max-width: 735px){
  h2{font-size: 24px;}

  h3{font-size: 18px;}
}

@media(max-width: 360px){
  h2{font-size: 21px;}
}

hr {border: 0.5px solid #444444; }

p {
  font-size: 16px;
  line-height: 1.5;
  color: #666; 
  margin-bottom: 15px;
}

ul {margin: 0; padding: 0;}

ul li {
  font-size: 16px;
  margin-bottom: 10px;
  line-height: 1.5;
  color: #343a40;
  position: relative;
  padding: 0;
}


ol {margin: 0; counter-reset: i; position: relative; }

ol li {
  font-size: 16px;
  margin-bottom: 10px;
  line-height: 1.5;
  color: #181d31;
  padding-left: 0;
  position: relative; 
}

a {
  color: #343a40;
  -webkit-transition: all ease-in-out 0.4s;
  transition: all ease-in-out 0.4s;
}

a:hover,
a:focus{
  text-decoration: none!important;
  color: #fe4e37;
  -webkit-transition: all 0.4s ease;
  transition: all 0.4s ease; 
}

input[type=text],
input[type=email],
input[type=number],
input[type=search],
input[type=password],
input[type=tel],
input[type=date],
input[type=time],
textarea,
select {
  font-size: 14px;
  font-weight: 300;
  background-color: #fff;
  border: 1px solid #eee;
  border-radius:0px;
  padding: 10px 20px;
  width: 100%;
  color: #444444;
  margin-bottom: 15px; 
  font-family: 'Lato', sans-serif;
  height: 42px;
  box-shadow: none;
  margin-bottom: 0;
  width: 100%;
}

input[type=text]:focus,
input[type=email]:focus,
input[type=number]:focus,
input[type=search]:focus,
input[type=password]:focus,
input[type=tel]:focus,
input[type=date]:focus,
textarea:focus,
select:focus {
  border-color: #ffac00;
  -webkit-transition: all 0.5s ease;
  transition: all 0.5s ease; 
}

.input-group .form-control{border-radius: 0;}

select {padding: 10px 20px; border-radius:0; width: 100%;}

select option{color: #444;}

textarea {border-radius: 0px; resize: vertical; height: 120px;}

label {
    display: inline-block;
    color: #666;
    margin-bottom: 8px;
    font-weight: 400;
    font-size: 15px;
}

img {max-width: 100%; }


blockquote {
  font-size: 14px;
  font-weight: 300;
  background-color: #fe4e37;
  margin-bottom: 15px;
  border: none;
  padding: 30px 70px 30px 70px;
  line-height: 24px;
  color: #fff;
  margin: 20px 0;
  font-style: italic;
  position: relative; 
}
blockquote:after{
  content: '';
  position: absolute;
  height: 60%;
  width: 2px;
  background: #fff;
  top: 20%;
  left: 35px;
}
blockquote p{
  color: #fff;
  margin: 0;
}
blockquote:before {
  content: '\f10d';
  font-family: fontawesome;
  font-size: 45px;
  position: absolute;
  bottom: 26px;
  right: 20px;
  color: #fff; 
  -webkit-transform: rotate(180deg); 
          transform: rotate(180deg);
}

blockquote span {position: relative; padding-left: 20px; }

blockquote span:before {
  content: '';
  width: 12px;
  height: 1px;
  background: #ffac00;
  position: absolute;
  left: 0;
  top: 50%;
