.container{position: relative; z-index: 1;}

section{padding: 89px 0 100px; position: relative; background: #fff;}

section:nth-child(odd){
  background: #f8f9fa;
}

section.icon-left:after{
  position: absolute;
  height: 500px;
  width: 100%;
  content: '';
  /* background: url('../images/bg-icon-1.html') no-repeat; */
  background-position: center;
  background-size: contain;
  top: 0;
  left: -45%;
  opacity: 0.7;
  -webkit-animation:heartbeat 5s cubic-bezier(0.245, 0.325, 0.51, 1.305) infinite alternate;
          animation:heartbeat 5s cubic-bezier(0.245, 0.325, 0.51, 1.305) infinite alternate;
}
section.icon-right:after{
  position: absolute;
  height: 400px;
  width: 80%;
  content: '';
  /* background: url('../images/bg-icon-2.html') no-repeat; */
  background-position: center;
  background-size: contain;
  top: 0;
  right: -40%;
  opacity: 0.4;
  -webkit-animation:heartbeat 5s cubic-bezier(0.245, 0.325, 0.51, 1.305) infinite alternate;
          animation:heartbeat 5s cubic-bezier(0.245, 0.325, 0.51, 1.305) infinite alternate;
}

.overlay{
    position: absolute;
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;
    background:rgba(0,0,0,0.7);
}
.color-overlay{
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  background:-webkit-gradient(linear, left top, right top, from(#1DB9A0), to(#4d94c4));
  background:linear-gradient(to right, #1DB9A0 0%, #4d94c4 
    100%);
  opacity: 0.4;
  -webkit-transition: all ease-in-out 0.5s;
  transition: all ease-in-out 0.5s;
}
