.section-title{
  text-align: center;
  width: 60%;
  margin: 0 auto 68px;
  position: relative;
  z-index: 1;
}
.title-white h2,
.title-white p{
  color: #fff;
}
.section-title span{
  color: #fe4e37;
}
.section-btn{
  text-align: center;
  padding-top: 80px;
}

@media(max-width: 991px){
  .section-title{width: 90%;}
}

@media(max-width: 480px){
  .section-title{width: 100%;}
}

/*pagination*/

.pagination-main{line-height:0.8;} 

.pagination {margin-top: 0px;margin-bottom: 0; }

.pagination li {padding-left: 0; padding-bottom: 0; margin-bottom: 0;  float: left;}

.pagination li:before, .pagination li:after {display: none !important; }

.pagination li a {
  height: 40px;
  line-height: 38px;
  margin-right: 9px;
  padding: 0;
  text-align: center;
  width: 40px;
  color: #444444;
  font-size: 12px;
  border-color: #bfbfbf; 
}

.pagination>li:first-child>a, .pagination>li:first-child>span,
.pagination>li:last-child>a, .pagination>li:last-child>span{border-radius: 0;}

.pagination li a:hover, .pagination li a:focus {
  background-color: #fe4e37;
  color: #FFFFFF!important;
  border-color: #fe4e37; 
}

.pagination .prev a,
.pagination .next a { border-radius: 0px;}

.pagination li.active a {
    background: #fe4e37 !important;
    border-color: #fe4e37 !important;
    color: #FFFFFF;
}

.pagination>li:last-child>a, .pagination>li:last-child>span{margin: 0;}

@media(max-width: 567px){
  .pagination li a {
    height: 30px;
    width: 30px;
    line-height: 28px;
  }
}

@media (max-width: 359px){
  .pagination li a {
      height: 28px;
      width: 28px;
      line-height: 28px;
  }
}

