section.breadcrumb-outer{
  background: url(/adeebAhmad1/Hotux.git/static/media/breadcrumb.776afce4.jpg) no-repeat;
  background-size: cover;
  background-position: center;
  position: relative;
  text-align: center;
  padding: 160px 0 60px;
}
.breadcrumb-outer:before{
  position: absolute;
  content: '';
  height: 100%;
  width: 100%;
  top: 0;
  left: 0;
  background: rgba(0,0,0,0.5);
}
.breadcrumb-content{
  position: relative;
  z-index: 1;
}
.breadcrumb-content h2{
  color: #fff;
  text-transform: uppercase;
}
.breadcrumb{
  background: transparent;
  margin: 0;
  padding: 0;
}
.breadcrumb li,
.breadcrumb li.active{
  margin-bottom: 0;
  color: #fff;
}

.breadcrumb li a, .breadcrumb li.active a{
  color: #fe4e37;
}

.breadcrumb>li+li:before{content: '|'; padding: 0 8px 0 5px;}

@media(max-width: 735px){
  section.breadcrumb-outer{padding: 120px 0 60px;}
}

/*Buttons*/

.btn, .btn#submit{
  padding: 12px 30px 12px;
  border: 1px solid #fff;
  border-radius: 20px;
  color: #fff;
  display: inline-block;
  -webkit-transition: all ease-in-out 0.3s;
  transition: all ease-in-out 0.3s;
  text-transform: uppercase;
  font-size: 12px;
}
.btn-orange{
  background: #fe4e37;
  color: #fff;
  border-color: #fe4e37;
}
.btn-orange:hover{
  color: #fe4e37;
  background: #fff;
}
.btn-black{
  background: #343a40;
  border-color: #343a40;
  color: #fff;
}
.btn-black:hover{
  color: #343a40;
  background: #fff;
}

.btn:focus, .btn:visited{outline: none; box-shadow: none;}

@media (max-width: 1100px){

  .btn{padding: 12px 18px 12px;}
}


#back-to-top {
  position: fixed;
  z-index: 1000;
  bottom: 20px;
  right: 20px;
  display: none;
}
#back-to-top a {
  display: block;
  width: 40px;
  height: 40px;
  background: #fe4e37;
  position: relative;
  border-radius: 50%;
  -webkit-transition: 0.2s all linear;
  transition: 0.2s all linear;
}
#back-to-top a:after{
  position: absolute;
  content: '\f35b';
  font-family:"Font Awesome 5 Free";
  font-size: 20px;
  color: #fff;
  top: 50%;
  left: 50%;
  z-index: 1;
  font-weight: 500;
  -webkit-transform: translate(-50%,-50%);
          transform: translate(-50%,-50%);
}

