/* Slider */

.slick-slider {
    position: relative;
    display: block;
    box-sizing: border-box;
    -webkit-user-select: none;
    -moz-user-select: none;
    -ms-user-select: none;
    user-select: none;
    -webkit-touch-callout: none;
    -khtml-user-select: none;
    touch-action: pan-y;
    -webkit-tap-highlight-color: transparent
}

.slick-list {
    position: relative;
    display: block;
    overflow: hidden;
    margin: 0;
    padding: 0
}

.slick-list:focus {
    outline: none
}

.slick-list.dragging {
    cursor: pointer;
    cursor: hand
}

.slick-slider .slick-track,
.slick-slider .slick-list {
    -webkit-transform: translate3d(0, 0, 0);
    transform: translate3d(0, 0, 0)
}

.slick-track {
    position: relative;
    top: 0;
    left: 0;
    display: block
}

/*.slick-track::before,
.slick-track::after {
    display: table;
    content: ''
}

.slick-track::after {
    clear: both
}
*/
.slick-loading .slick-track {
    visibility: hidden
}

.slick-slide {
    display: none;
    float: left;
    height: 100%;
    min-height: 1px
}

[dir='rtl'] .slick-slide {
    float: right;
}

.slick-slide img {
    display: block
}

.slick-slide.slick-loading img {
    display: none
}

.slick-slide.dragging img {
    pointer-events: none;
}

.slick-initialized .slick-slide {
    display: block
}

.slick-loading .slick-slide {
    visibility: hidden
}

.slick-vertical .slick-slide {
    display: block;
    height: auto;
    border: 1px solid transparent
}


/* Slider */


/* Arrows */

.slick-prev,
.slick-next {
    font-size: 0;
    line-height: 0;
    position: absolute;
    top: 50%;
    display: block;
    width: 40px;
    height: 40px;
    margin-top: -20px;
    padding: 0;
    cursor: pointer;
    color: transparent;
    border: none;
    outline: none;
    background: #fff;
    line-height: 40px;
    z-index: 1;
    -webkit-transition: all ease-in-out 0.3s;
    transition: all ease-in-out 0.3s;
    border-radius: 50%;
}

.slick-prev:hover,
.slick-prev:focus,
.slick-next:hover,
.slick-next:focus {
    color: transparent;
    outline: none;
    background: #333;
}

.slick-prev:hover::before,
.slick-prev:focus::before,
.slick-next:hover::before,
.slick-next:focus::before {
    opacity: 1
}

.slick-prev.slick-disabled::before,
.slick-next.slick-disabled::before {
    opacity: 0
}

.slick-prev::before,
.slick-next::before {
    font-family: 'fontawesome';
    font-size: 20px;
    line-height: 2.1;
    opacity: 1;
    color: #fe4e37;
    -webkit-font-smoothing: antialiased;
    -moz-osx-font-smoothing: grayscale
}

.slider-section .slick-prev, .slider-section .slick-next {
    left: 44%;
    background: #fff;
    height: 50px;
    width: 50px;
    z-index: 10;
    top: 15%;
    padding: 5px 5px 5px 0;
    box-shadow: 0 0 10px #ccc;
    z-index: 1;
}

.slider-section .slick-next {
    right: 44%;
    left: inherit;
    padding: 5px 0px 5px 5px;
}

.slider-section .slick-prev::before, .slider-section .slick-next::before{font-size: 36px;}

.slick-prev {
    left:15px;
}

[dir='rtl'] .slick-prev {
    right: 15px;
    left: auto
}

.slick-prev::before {
    content: '\f177'
}

[dir='rtl'] .slick-prev::before {
    content: '\f105'
}

.slick-next {
    right: 15px;
}

[dir='rtl'] .slick-next {
    right: auto;
    left: 15px;
}

.slick-next::before {
    content: '\f178'
}

[dir='rtl'] .slick-next::before {
    content: '\f104'
}

