/*          Banner CSS        */
/* ======================================== */

section.banner{
	padding: 0;
    position: relative;
}
a.slider-btn{
	padding: 15px 25px 15px;
	display: inline-block;
	background: #fe4e37;
	color: #fff;
	border-radius: 20px;
	text-transform: uppercase;
}
a.slider-btn:hover{
	background: #fff;
	color: #fe4e37;
}
a.btn-wt{
	background: #fff;
	color: #333;
}
.slider{
    height: 675px;
    overflow: hidden;
    position: relative;
}
.swiper-slide::after{
    content: '';
    position: absolute;
    height: 100%;
    width: 100%;
    background: rgba(0,0,0,0.5);
    top: 0;
    left: 0;
}
  .swiper-container {
      width: 100%;
      height: 100%;
    }
    .swiper-slide {
      text-align: center;
      background: #fff;

      /* Center slide text vertically */
      display: flex;
      justify-content: center;
      align-items: center;
      background-size: 100%;
    background-position: top center;
    }

.slider .swiper-container-horizontal>.swiper-pagination-bullets, 
.slider .swiper-pagination-custom, .slider .swiper-pagination-fraction{bottom: 40px;}

.slider .swiper-pagination-bullet{
    width: 10px;
    height: 10px;
    background: #fff;
    opacity: 1;
}
.slider .swiper-pagination-bullet-active{
    background: #fe4e37;
}
.slider .swiper-container-vertical>.swiper-pagination-bullets{
    right: 50px;
}
.swiper-content{
    z-index: 1;
    position: absolute;
    top: 50%;
    left: 50%;
    -webkit-transform: translate(-50%,-50%);
            transform: translate(-50%,-50%);
    text-align: center;
}
.swiper-content img {
  margin: 0 auto;
}
.swiper-content h3{
    color: #fff;
    margin: 16px 0 12px;
    font-size: 15px;
    position: relative;
    padding: 0;
    line-height: normal;
    font-weight: normal;
    display: inline-block;
}
.swiper-content h3::before{
    position: absolute;
    content: '';
    height: 1px;
    width: 50px;
    background: #fff;
    top: 10px;
    left: -60px;
}
.swiper-content h3::after{
    position: absolute;
    content: '';
    height: 1px;
    width: 50px;
    background: #fff;
    top: 10px;
    right: -60px;
}
.swiper-content h1{
    color: #FFF;
    font-size: 60px;
    font-weight: 600;
    line-height: normal;
    text-transform: uppercase;
    margin-bottom: 20px;
}
.swiper-content h1 span{
    color: #fe4e37;
}

.swiper-content p{
    color: #fff;
    width: 70%;
    margin: 0 auto;
}
.slider-text{
    padding-top: 100px;
}

#vid{
position: absolute;
  top: 50%; 
  left: 50%;
  -webkit-transform: translateX(-50%) translateY(-50%);
  transform: translateX(-50%) translateY(-50%);
  min-width: 100%; 
  min-height: 100%; 
  width: auto; 
  height: auto;
  z-index: 0; 
  overflow: hidden;
}

